.card {
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: auto;
  width: 500px;
  height: 250px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0 10px 15px 8px rgba(0, 0, 0, 0.2);
  position: fixed;

  .content {
    margin: 20px;
  }

  .content h2 {
    text-align: center;
  }
  img {
    position: absolute;
    border-radius: 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  }
  .info {
    width: 350px;
    float: left;
    position: absolute;
    left: 150px;
    top: 80px;

    span {
      margin: 0 15px;
      font-size: 15px;
      font-weight: bold;
      color: rgb(85, 85, 85);
    }

    .data span + span {
      float: right;
    }
  }
}

.closeBtn {
  float: left;
  cursor: pointer;
  color: rgb(114, 114, 114);
  border: none;
  font-weight: bold;
  background: transparent;
  font-size: 20px;
  transition: all 0.3s ease;
  text-align: center;
  border-radius: 5px 0 0;
}
.closeBtn:hover {
  background: rgb(85, 85, 85);
  color: rgba(255, 255, 255, 0.95);
}
